//commonJsrequiremodule.exportsexports
const fetch  = require('./config.js')

module.exports = {
	//token
	/** token */
	// usernameuserName-shu  10801-shu
	// password123456        
	// grant_typepassword     
	// client_idsmart-heart     
	// client_secretsmart-heart-secret   
	getTokenData(data){
		return fetch({
			url: "/auth/oauth/token",
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			method: "POST",
			data,
		})
	},
	// userId token
	postCode(data){
		return fetch({
			url: '/user-api/user/wechat/binding',
			method: 'POST',
			data
		})
	},
	// 
	bindMobile(data){
		return fetch({
			url: '/user-api/user/wechat/phone',
			method: 'POST',
			data
		})
	 },
	 // 
	deliveringToken(code){
		 return fetch({
			 url:'/wx/user/wx79d70ef10138edc1/login?code=' + code,
		 })
	 },
	userDelivering(data){
		 return fetch({
			 url:'/wx/user/wx79d70ef10138edc1/device/bind',
			 method:'POST',
			 data,
			 header: {
			   'Content-Type': 'application/json',
			   "token": data.token
			 }
		 })
	 },
	/**  */
	//   token
	getBanners(type){
		return fetch({
			url: "/about-api/pic/wechat/banner/" + type,
			header:{
				'content-type':"application/json"
			}
		})
	},
	//  token
	getNearest(location){
		let {
			longitude,
			latitude
		}=location
		return fetch({
			url: '/station-api/station/wechat/' + longitude + '/' + latitude,
		})
	},
	//  /***/
	getClassify(id){
		return fetch({
			url: '/station-api/resource/' + id
		})
	},
	// 
	getNearestKc(location){
		let {
			longitude,
			latitude
		}=location
		return fetch({
			url: '/kitchen/fl/' + longitude + '/' + latitude
		})
	},
	// 
	myMoney(userId){
		return fetch({
			url: '/user-api/user/wechat/user/' + userId
		})
	},
	// 
	getAllRank(){
		return fetch({
			url: '/user-api/user/wechat/rank'
		})
	},
	// 
	getRankList(type){
		return fetch({
			url: '/user-api/user/wechat/rank/' + type
		})
	},
	
	/**  */
	// 
	getKwStationList(){
		return fetch({
			url: '/kitchen/fl/getKwStationList'
		})
	},
	// 
	getAllStations(){
		return fetch({
			url: '/station-api/station/'
		})
	},
	// 
	getKwStaion(station){
		let url='/kitchen/fl/getKwStaion/' + station.sno + '/' + station.lng + '/' + station.lat
		return fetch({
			url
		})
	},
	// 
	getSingleStation(station){
		let url='/station-api/station/' + station.id + '/' + station.lng + '/' + station.lat
		return fetch({
			url
		})
	},
	
	
	/**  */
	// 
	getclassifyList(){
		return fetch({
			url: '/station-api/resourceShow/wechat/type'
		})
	},
	
	/**  */
	//
	getDailyTask(today){
		return fetch({
			url: '/user-api/user/wechat/daily/' + today
		})
	},
	//  
	wechatScore(data){
		return fetch({
			url: '/user-api/user/wechat/score',
			method: "POST",
			data
		})
	},
	
	/**  */
	// 
	productList(data){
		return fetch({
			url: '/score/product/getList',
			method: "POST",
			data
		})
	},
	// 
	addOrder(data){
		return fetch({
			url:"/score/order/wechat/add",
			method:"POST",
			data
		})
	},
	// 
	updateOrder(data){
		return fetch({
			url:'/score/order/wechat/update',
			method:"POST",
			data
		})
	},
	// 
	getOrderList(data){
		return fetch({
			url: '/score/order/wechat/list/' + data.page + '/' + data.pageSize + '/' + data.state
		})
	},
	// 
	getAddress:(userId) => {
		return fetch({url:'/user-api/user/getUserAdress/' + userId})
	},
	
	
	
	/**  */
	// 
	// phone -->  
	// type =1  type=2 type=3
	getCode(data){
		return fetch({
			url: '/user-api/phoneCode/' + data.phone + '/' + data.type
		})
	},
	// 
	phoneLogin(data){
		return fetch({
			url: '/user-api/phoneLogin/' + data.phone + '/' + data.code
		})
	},
	// / 
	checkCode(data){
		return fetch({
			url: '/user-api/checkCode/' + data.phone + '/' + data.code
		})
	},
	// 
	updatePwd(data){
		return fetch({
			url: '/user-api/updatePwd/' + data.phone + '/' + data.pwd
		})
	},
	//
	passwordLogin(data){
		return fetch({
			url: '/user-api/userByphone/' + data.phone + '/' + data.pwd
		})
	},
	//
	// 
	// openid ----->app openid
	// type -------> 2iosAPP  3Android APP
	wechatLogin(data){
		return fetch({
			url: '/user-api/wechatLogin/',
			method:'POST',
			data
		})
	},
	// 
	// 
	// phone  --->
	// code  ----->
	// openid ----->app openid
	// type -------> 2iosAPP  3Android APP
	bindPhone(data){
		return fetch({
			url: '/user-api/bindPhone/',
			method:'POST',
			data
		})
	},
	
	/***/
	fundCash: (amount) => {
	    return fetch({
			url:'/fund-common-api/fund/cash', 
			method:'post', 
			data: { amount },
		})
	},
	// 
	applyMsg: (sno) => {
	    return fetch({
			url:'/fund-common-api/fund/' + sno
		})
	},

}

