export const IMG_URL = 'https://heart-score-mall.oss-cn-hangzhou.aliyuncs.com/goods/'

export const goLoginPageTimeOut = (str="")=>{
	uni.showModal({
		title:"",
		content:str,
		confirmText: '',
		confirmColor: '#1E1E1E',
		success:(res)=>{
			if (res.confirm) {

				uni.navigateTo({
					url:"/pages/Authorize/wx/index"
				})






			}
		}
	})
	uni.removeStorageSync('token')
	uni.removeStorageSync('isLogin')
}